export const getdepartmentlist = (departmentId) => {

    let params = {
        token: sessionStorage.getItem("token"),
        userid: sessionStorage.getItem("userId"),
        departmentid:departmentId,
    }
    return Api.requestForm2('/department/find', params)
        .then(res => res);
}


